import React from 'react';
import {
  StreamlitComponentBase,
  withStreamlitConnection
} from "streamlit-component-lib";
import SignIn from './components/sign-in/sign-in.component'

import './App.scss'

class App extends StreamlitComponentBase {
  render = () => {
      const headerText =  this.props.args['headerText'];
      const logo = this.props.args['logo']
      const loginEndpoint = this.props.args['AD_URL']
      const prefix = this.props.args['prefix']

      if (!loginEndpoint || loginEndpoint === '') return null

      return (
        <div className="App">
          <SignIn headerText={headerText} loginEndpoint={loginEndpoint} logo={logo} prefix={prefix} />
        </div>
      )
  }
}

export default withStreamlitConnection(App)