const statusCodes = {
  GET:      [200],
  POST:     [200, 201],
  PUT:      [200],
  DELETE:   [200, 204],
  // OPTIONS:  [httpStatus.OK, httpStatus.NO_CONTENT]
}

class Api {
  static headers() {
    return {
      'Content-Type': 'application/json',
    }
  }

  static async get(route) {
    return this.xhr(route, null, 'GET');
  }

  static async put(route, params) {
    return this.xhr(route, params, 'PUT');
  }

  static async post(route, params, host) {
    return this.xhr(route, params, 'POST', host);
  }

  static async delete(route, params) {
    return this.xhr(route, params, 'DELETE');
  }

  static async xhr(route, params, verb, externalHost) {
    const host = externalHost;
    const url = `${host}${route}`;
    let options = Object.assign({ method: verb }, params ? { body: JSON.stringify(params) } : null );
    options.headers = Api.headers();
    return fetch(url, options).then( resp => {
      let json = resp.status !== 204 ? resp.json() : {}
      const expectedCode = statusCodes[verb];
      if (expectedCode.includes(resp.status)) {
        return json;
      }
      return json.then(err => {throw err});
    });
  }
}
export default Api
