import React from 'react';

import './form-input.styles.scss';

class FormInput extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true
    }
    this.inputRef = React.createRef();
    this.showToggle = this.showToggle.bind(this);
  }

  componentDidMount() {
    const { name } = this.props;
  }

  showToggle() {
    this.setState({
      show: !this.state.show
    });
  }

  render() {
    const {
      onChange, label, shrink, showHideFlag, error, type, name,
      value, inputFocused,
      ...otherProps
    } = this.props;

    return (
      <div className='group'>
        <input
          ref={this.inputRef}
          className={`form-input
            ${error ? 'isError' : ''}
            ${inputFocused === name ? 'keyboardfocus' : ''}
          `}
          onChange={onChange}
          name={name}
          value={value}
          // onFocus={() => setFocus(name)}
          type={
            showHideFlag
              ? this.state.show
                ? 'password'
                : 'input'
              : type
            }
          {...otherProps}
        />
        {
          label
            ? (<label className={`${shrink ? 'shrink' : ''} form-input-label`}>
                 {label}
              </label>)
            : null
        }
        {
          label
            ? (<label className='form-input-label error-message' >
                 { error }
              </label>)
            : null
        }
        {
          showHideFlag
            ? (<label className={`form-input-label-show`} onClick={this.showToggle}>
                { this.state.show
                  ? 'Show'
                  : 'Hide'
                }
              </label>)
            : null
        }
      </div>
    );
  }
}

export default FormInput;