import React from 'react'

const FormWithValidationAndSpinner = ({ children, className, onSubmit, isValidForm }) => {
  const submitHandler = async (e) => {
    e.preventDefault()
    const isValid = await isValidForm()
    if (isValid) {
      onSubmit(e)
    }
  }

  return (
    <form className={className} onSubmit={submitHandler} autoComplete="off">
      { children }
    </form>
  )
}

export default FormWithValidationAndSpinner