import React, { useState, useEffect } from 'react';
import { Streamlit } from "streamlit-component-lib"

import FormWithValidation from '../../components/form-with-validation/form-with-validation.component';
import FormInput from '../form-input/form-input.component';
import CustomButton from '../custom-button/custom-button.component';
import { Spinner } from '../spinner/spinner.component';
// import logo from '../../assets/images/logo-intercement.png';

import Joi from 'joi';

import Api from '../../lib/Api'

// import ActiveDirectory from 'activedirectory';
import './sign-in.styles.scss';

// Define the schema to validate the form
const schema = Joi.object().keys({
  username: Joi.string().max(15)
    .required()
    .messages({
      'string.empty': `Username is not allowed to be empty`,
      'string.max': `Username should have a maximum length of {#limit}`,
      'any.required': `Username is a required field`
    }),
    password: Joi.string().required().max(15).messages({
      'string.empty': `Password is not allowed to be empty`,
      'string.max': `Password should have a maximum length of {#limit}`,
      'any.required': `Password is a required field`
  })
})

const SignIn = ({ headerText, loginEndpoint, logo, prefix}) => {
  const [username, setUsername] = useState()
  const [password, setPassword] = useState()
  const [loading, setLoading] = useState(false)
  const [errorMessage, setErrorMessage] = useState()
  const [isDirtyForm, setIsDirtyForm] = useState(false)
  const [fieldErrorMessages, setFieldErrorMessages] = useState()

  useEffect(() => {
    Streamlit.setFrameHeight(600)
  }, [])

  useEffect(() => {
    if (isDirtyForm) {
      validateForm()
    }
  }, [username, password])

  const changeProp = (name, value) => {
    setErrorMessage(undefined)

    switch (name) {
      case 'username':
        setUsername(value)
        break
      case 'password':
        setPassword(value)
        break
    }
  }

  const getFieldErrorMessage = (fieldName) => {
    if (!isDirtyForm) return undefined

    const field = fieldErrorMessages?.find(fi => fi.field === fieldName)
    return field?.message
  }

  const changeHandler = (fieldName, e) => {
    changeProp(fieldName, e.target.value)
  }

  const validateForm = () => {
    setIsDirtyForm(true)
    let isValid = false
    let fieldErrorMessages = []

    const valueSet = {
      username,
      password
    }

    const validationOptions = {
      abortEarly: false   // Validate all fields, not abort in the first error
    }
    const { error } = schema.validate(valueSet, validationOptions)

    if (error && error?.details?.length > 0) {
      error.details.map((error) => {
        const field = error.context.key
        fieldErrorMessages = [...fieldErrorMessages, { field, message: error.message }]
      })
      setFieldErrorMessages(fieldErrorMessages)
    } else {
      isValid = true
      setFieldErrorMessages(undefined)
    }

    return isValid
  }

  const isValidForm = async () => {
    return await validateForm()
  }

  const handleSubmit = async (event) => {
    setLoading(true)

    const loginBody = {
      name: `${prefix ? `${prefix}\\` : ''}${username}`,
      password
    }

    try {
      return Api.post(`/login`, loginBody, loginEndpoint)
        .then(res => {
          Streamlit.setComponentValue(res?.token)
          return res && res.token
            ? res.token
            : undefined
        })
        .catch(error => {
          setErrorMessage(error ? error?.message : 'Something was wrong')
          return undefined
        });
    } catch (err) {
      console.log('error', err)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className='sign-in-container'>
      <div className='logo-contanier'>
        <img className='logo-image' alt='' src={logo} />
      </div>
      { headerText &&
        <p className='welcome-message'>
          { headerText }
        </p>
      }
      <div className='form-container'>
        <FormWithValidation
          onSubmit={handleSubmit}
          isValidForm={isValidForm}
        >
          <FormInput
            type='text'
            name='username'
            onChange={(e) => changeHandler('username', e)}
            label='Usuario'
            maxLength='30'
            error={getFieldErrorMessage('username')}
            disabled={loading}
            shrink
          />
          <FormInput
            type='password'
            name='password'
            onChange={(e) => changeHandler('password', e)}
            label='Password'
            maxLength='30'
            error={getFieldErrorMessage('password')}
            disabled={loading}
            shrink
            showHideFlag
          />
          { errorMessage && errorMessage !== '' &&
            <div className='error-message'>{ errorMessage }</div>
          }
          <div className='footer-container'>
            <CustomButton type='submit' disabled={loading}>
              { !loading ? 'Login' : <Spinner /> }
            </CustomButton>
          </div>
        </FormWithValidation>
      </div>
    </div>
  )
}

export default SignIn;